

.product{
    cursor: pointer;
    margin-bottom: 2rem;

}
.product img{
    transition: 0.3s all;
}
.product:hover img{
        opacity: 0.7;  
        background: orangered;
        transform: translateY(20px);
        opacity: 0;
        transition: 0.9s all;  
}
.product:hover .buy-btn {
    background: orangered;
    transform: translateY(20px);
    opacity: 0;
    transition: 0.9s all;
}
hr{
    width: 40px;
    height: 2px;
    background-color: g;
    border: 1px solid red;

}
.star{
    padding :10px 0;
}
.star i{
    font-size: 0.8rem;
    color: goldenrod;

}
